import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { ModuleWithProviders } from "@angular/core";
import { DashboardComponent } from "./features/dashboard/dashboard.component";
import { DialogComponent } from "./features/modal/dialog.component";
import { AuthenticationGuard } from "./core/authentication/authentication.guard";
import { AuthLayoutComponent } from "./shared/layout/app-layouts/auth-layout.component";
import { MainLayoutComponent } from './shared/layout/app-layouts/main-layout.component';
import { InfoProdukComponent } from './features/info-produk/info-produk.component';
import { DetailProdukComponent } from './features/detail-produk/detail-produk.component';
import { VerifikasiKtpelComponent } from './features/verifikasi-ktpel/verifikasi-ktpel.component';
import { CekinNasabahComponent } from './features/cekin-nasabah/cekin-nasabah.component';
import { DetailVerifikasiComponent } from './features/detail-verifikasi/detail-verifikasi.component';
import { LayarVerifikasiComponent } from './features/layar-verifikasi/layar-verifikasi.component';
import { DetailChannelComponent } from './features/detail-channel/detail-channel.component';

const routes: Routes = [
  {
    path: "",
    component: MainLayoutComponent,
    data: { pageTitle: "Home" },
    canActivate: [AuthenticationGuard],
    children: [
      {
        path: "",
        redirectTo: "dashboard",
        pathMatch: "full",
      },
      {
        path: "dashboard",
        component: DashboardComponent,
      },

      {
        path: "modal",
        component: DialogComponent,
      },
      {
        path: "infoProduk",
        component: InfoProdukComponent,
      },
      {
        path: "detail-produk",
        component: DetailProdukComponent,
      },
      {
        path: "verifikasi-ktpel",
        component: VerifikasiKtpelComponent,
      },
      {
        path: "cekin-nasabah",
        component: CekinNasabahComponent,
      },
      {
        path: "detail-verifikasi",
        component: DetailVerifikasiComponent,
      },
      {
        path: "layar-verifikasi",
        component: LayarVerifikasiComponent,
      },
      {
        path: "detail-channel",
        component: DetailChannelComponent,
      },
    ],
  },
  // { path: "", redirectTo: "dashboard", pathMatch: "full" },
  // { path: "dashboard", component: DashboardComponent },
  //{ path: "login", component: LoginComponent },
  {
    path: "auth",
    component: AuthLayoutComponent,
    loadChildren: () => import("./features/auth/auth.module").then((m) => m.AuthModule),
  },

  // { path: "modal", component: DialogComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule],
})
export class AppRoutingModule {}

