import { Component, OnInit } from "@angular/core";
import { Router, CanActivate } from "@angular/router";
import { StorageUtilService } from "./../../shared/utils/storage-util.service";
import { InfoProdukService } from "./../../core/services/infoproduk.service";
import { NasabahService } from "./../../core/services/nasabah.service";

import * as _ from "lodash";
import { ErrorCodeConstants } from "./../../core/enums/errcode-constants.enum";
import { SettingCodeConstants } from "./../../core/enums/setting-constants.enum";
import { ConfirmationService } from "./../../shared/confirmdialog/confirmationservice";
@Component({
  selector: "app-dashboard",
  templateUrl: "./dashboard.component.html",
})
export class DashboardComponent implements OnInit {
  date = new Date().getFullYear();
  isKomparasi = false;
  btnBandingkanValid = false;
  selected = [];
  display = false;
  credentialsNasabahKey = "nasabahCredentials";
  userDomainCredentialKey = "userdomain";
  private _credentials: any | null;
  private userdomain: any | null;
  namaNasabah = "";
  listKategori: any;
  listInfoProduk: any;
  selectedKategori: string;

  constructor(
    private router: Router,
    private confirmationService: ConfirmationService,
    private infoProdukServices: InfoProdukService,
    private nasabahServices: NasabahService
  ) {
    if (StorageUtilService.hasItem(this.credentialsNasabahKey)) {
      this._credentials = StorageUtilService.getItem(
        this.credentialsNasabahKey
      );
    }
    if (StorageUtilService.hasItem(this.userDomainCredentialKey)) {
      this.userdomain = StorageUtilService.getItem(
        this.userDomainCredentialKey
      );
      console.log("this.userdomain", this.userdomain);
    }
  }

  ngOnInit() {
    this.infoProdukServices.getInfoProduk().subscribe(
      (response) => {
        // console.log(response)
        this.listInfoProduk = _.chain(response.output_schema)
          .groupBy("kategori")
          .map((value, key) => ({ kategori: key, products: value }))
          .value();

        this.selectedKategori = SettingCodeConstants.simpanan;
        console.log(this.listInfoProduk);
      },
      (error) => {
        // this.loading = false;
        // this.messageService.add({
        //   severity: 'error',
        //   summary: 'Gagal Mengambil Service',
        //   detail: error.message,
        //   key: 'tabel-message'
        // });
      }
    );
  }

  showDialog() {
    this.namaNasabah = "";
    this.display = true;
  }
  checkKomparasi(event: any) {
    console.log(event);
    if (event === "A") {
      this.selected = [];
    } else {
      this.selected = [];
      this.btnBandingkanValid = false;
    }
  }

  // check if the item are selected
  checked(item) {
    if (this.selected.indexOf(item) != -1) {
      return true;
    }
  }

  // when checkbox change, add/remove the item from the array
  onChange(checked, item) {
    if (checked) {
      this.selected.push(item);
    } else {
      this.selected.splice(this.selected.indexOf(item), 1);
    }

    if (this.selected.length > 1) {
      this.btnBandingkanValid = true;
      console.log(this.selected);
    } else {
      this.btnBandingkanValid = false;
    }
  }

  onChangeKategori(kategori: string) {
    this.selectedKategori = kategori;
    this.selected = [];
    console.log("changeKategori", kategori);
  }

  actionNext() {
    
    //console.log(this.selected.join(', '))

    StorageUtilService.setItem(this.credentialsNasabahKey, {
      namaNasabah: this.namaNasabah,
      kategori: this.selectedKategori,
      products: this.selected,
    });

    // call services checkin Nasabah

    return new Promise((resolve, reject) => {
      this.nasabahServices
        .checkInNasabah(
          "Info Produk",
          this.selected.join(', '),
          this.namaNasabah,
          this.userdomain.user_domain,
          this.userdomain.branch_code
        )
        .subscribe(
          (resp) => {
            // console.log("resp", resp);
            if (resp.error_schema.error_code == ErrorCodeConstants.G20000) {
              this.display = false;
              if (this.btnBandingkanValid) {
                this.router.navigate(["infoProduk"], { replaceUrl: true });
              } else {
                this.router.navigate(["detail-produk"], { replaceUrl: true });
              }
            }
          },
          (err) => {
            this.display = false;

            if (err.error instanceof ErrorEvent) {
              var errorCode = err.error.error_schema.error_code;
              var errorMessage = err.error.error_schema.error_message;
              if (errorCode == ErrorCodeConstants.G29999) {
                this.confirmationService.confirm({
                  icon: "./assets/img/Failed.png",
                  acceptIcon: null,
                  rejectIcon: null,
                  rejectVisible: true,
                  acceptVisible: true,
                  acceptLabel: "ULANGI",
                  rejectLabel: "BATAL",
                  header: errorCode,
                  message: errorMessage,
                  closable: true,
                  accept: () => {
                    this.actionNext();
                    //this.login(username, password)
                  },
                });
              }
              reject(err);
            } else {
              this.confirmationService.confirm({
                icon: "./assets/img/Failed.png",
                acceptIcon: null,
                rejectIcon: null,
                rejectVisible: true,
                acceptVisible: true,
                acceptLabel: "ULANGI",
                rejectLabel: "BATAL",
                header: ErrorCodeConstants.G29999,
                message: ErrorCodeConstants.G29999_Message,
                closable: true,
                accept: () => {
                  this.actionNext();
                  //this.login(username, password)
                },
              });

              reject(err);
            }
          }
        );
    });
    //end call services
  }

  showDialogPilih(productId) {
    this.selected = [productId];
    this.display = true;
  }
}
