import { Injectable } from "@angular/core";
import { Observable, of, throwError } from "rxjs";
import { catchError, tap, map } from "rxjs/operators";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { ErrorCodeConstants } from "../enums/errcode-constants.enum";
import { Logger } from "../logger.service";

import { environment } from "./../../../environments/environment";
import { ConfirmationService } from "./../../shared/confirmdialog/confirmationservice";
import { StorageUtilService } from "./../../shared/utils/storage-util.service";
const log = new Logger("NasabahService");

const httpOptions = {
  headers: new HttpHeaders({
    "Content-Type": "application/json",
    // "Operation": "login",
    // 'Access-Control-Allow-Methods': 'POST, GET, PUT, OPTIONS, DELETE',
    Accept: "application/json, text/plain, */*",
    //"Operation" : "checkInCustomer",
  }),
};

@Injectable()
export class NasabahService {
  isLoggedIn: boolean = false;
  private serverUrl = environment.BASE_API_URL;
  private url = environment.URL;

  constructor(
    private http: HttpClient,
    private confirmationService: ConfirmationService
  ) {}
  
  


  checkInNasabah(
    source: string,
    detail: string,
    name: string,
    cso_id: string,
    branch: string
  ) {
    //getTotal(satkerId: string, idDistCoa: number, mataUang: string) {
    const body = { source, detail, name, cso_id, branch };
    return this.http
      .post<any>(this.serverUrl + "/" + this.url , body, httpOptions)
      .pipe(
        map((data: any) => data),
        catchError(this.handleError)
      );
    

    // return this.http
    //   .post(this.serverUrl + 'url', httpOptions,)
    //   .pipe(catchError(this.handleError));
  }

  private handleError(error: any): Observable<any> {
    // console.error("An error occurred", error);
    // console.log(error.error.error_schema.error_code);
    // error.message || error
    return throwError(error);
  }
}
