  onLogout() {
    // console.log('masuk');
    document.getElementById('myP1').style.display = 'none';
    this.confirmationService.confirm({
      icon: './assets/img/Info_Circle.png',
      acceptIcon: null,
      rejectIcon: null,
      rejectVisible: true,
      acceptVisible: true,
      acceptLabel: 'YA',
      rejectLabel: 'TIDAK',
      header:  '',
      message: 'Lanjut Sign Out',
      closable: false,
      accept: () => {
        this.router.navigate(['/auth/login']);
      },
      reject: () => {
        document.getElementById('myP1').style.display = 'none';
      }
    });
  }
