import { Injectable } from '@angular/core';

declare var $: any;

@Injectable()
export class NotificationService {

  constructor() {
  }

  smallBox(data, cb?) {
    $.smallBox(data, cb)
  }

  bigBox(data, cb?) {
    $.bigBox(data, cb)
  }

  smartMessageBox(data, cb?) {
    $.OgyaMessageBox(data, cb)
  }

  smartMessageBoxClose() {
    $.OgyaMessageBoxClose()
  }

  errorMessageBox(content) {
    setTimeout(() => {
      this.smartMessageBox({
        title: '',
        content: '<span style=\'font-size:18px\'> ' + content + '</span>',
        buttons: '[Close]',
        ButtonOneCss: 'btn btn-primary btn-sm botTempo'
      }, (ButtonPressed) => {
        if (ButtonPressed === 'Close') {
          console.log('Close')
          this.smartMessageBoxClose();
        }
      });
    },
      500);
  }

  cancelMessageBox(content) {
    this.smartMessageBox({
      title: '',
      content: '<span style=\'font-size:18px\'> ' + content + ' </span>',
      buttons: '[Batal][OK]',
      ButtonOneCss: 'btn btn-success btn-sm botTempo',
      ButtonTwoCss: 'btn btn-danger btn-sm botTempo',
      NormalButton: 1
    }, (ButtonPressed) => {
      if (ButtonPressed === 'OK') {
        window.history.back();
        this.smartMessageBoxClose();
      } else if (ButtonPressed === 'Batal') {
        this.smartMessageBoxClose();
      }
    });
  }

}
