//import {Component, Input,Output,ElementRef,EventEmitter,OnInit} from '@angular/core';
import {NgModule,Component,ElementRef,OnDestroy, OnInit,Input,Output,EventEmitter,Renderer2,
  ContentChildren,QueryList,ViewChild,NgZone, ChangeDetectorRef,ViewRef,ChangeDetectionStrategy} from '@angular/core';
import {Header,Footer,SharedModule} from 'primeng/api';
@Component({
    selector: 'ImageModal',
    styleUrls: ['./css/style.css'],
   template: `

   <div class="ng-gallery" *ngIf="showRepeat"> 
     <div *ngFor ="let i of modalImages; let index = index">
       <img src="{{ i.thumb }}" class="ng-thumb" (click)="openGallery(index)" alt="Image {{ index + 1 }}" />
      </div>
   </div>

   <div class=" ui-dialog-mask ui-widget-overlay ui-dialog-visible ui-dialog-mask-scrollblocker "
   style="z-index: 1001;" *ngIf="opened">

    <div pfocustrap="" role="dialog"
        class="ng-trigger ng-trigger-animation  ui-dialog ui-widget ui-widget-content ui-corner-all ui-shadow ui-dialog-resizable "
       
        aria-labelledby="ui-dialog-1-label"
        style="width: 852px; height: 581px; overflow: visible; z-index: 1002;">

        <div class="ui-dialog-image-titlebar ui-widget-header ui-helper-clearfix ui-corner-top "
            style="">

            <div class="ui-dialog-image-title flex justify-center" id="ui-dialog-1-label" style="width: 800px; padding-left: 40px">
            <a class="nav-left cursor-pointer" *ngIf="modalImages.length >1" (click)="prevImage()"><i class="arrow-left-white" style="width:30px; cursor: pointer"></i></a>
            <span class="info-text">{{ currentImageIndex + 1 }}/{{ modalImages.length }} - Image
            {{currentImageIndex+1}}</span>
            <a class="nav-right cursor-pointer" *ngIf="modalImages.length >1" (click)="nextImage()"><i class="arrow-right-white" style="width:30px; cursor: pointer"></i></a>
            </div>
            <div class="ui-dialog-titlebar-icons">
                <a role="button" tabindex="0"
                    class=" ui-dialog-titlebar-icon ui-dialog-titlebar-close ui-corner-all "
                    >
                    <img  (click)="closeGallery()" height="28px" width="28px" src="./assets/img/ClosePopupImage.png" />
                </a>
            </div>
        </div>
        <div class=" ui-dialog-content ui-widget-content">
          <div id="flex justify-center">
            <div class="ng-gallery-content">
              <div class="uil-ring-css" *ngIf="loading">
                  <div></div>
              </div>
             
             
              <img *ngIf="!loading" src="{{imgSrc}}" (click)="nextImage()" class="effect" />
             
              <span class="info-text">{{ currentImageIndex + 1 }}/{{ modalImages.length }} - Image
                  {{currentImageIndex+1}}</span>
          </div>
          </div>
        </div>
        <div #footer class="ui-dialog-footer ui-widget-content" *ngIf="footerFacet && footerFacet.first">
            <ng-content select="p-footer"></ng-content>
        </div>
        
        <div class="ui-resizable-handle ui-resizable-se ui-icon ui-icon-gripsmall-diagonal-se  "
            style="z-index: 90;"></div>
    </div>
</div>

   
   
       ` 
})
export class ImageModal implements OnInit {
   public _element:any;
   public opened:boolean = false;
   public imgSrc:string;
   public currentImageIndex:number;
   public loading:boolean= false;
   public showRepeat:boolean= false;
  @Input('modalImages') public modalImages:any;
  @Input('imagePointer') public imagePointer:number;
  @Output('cancelEvent') cancelEvent = new EventEmitter<any>();
  @ContentChildren(Footer, {descendants: false}) footerFacet: QueryList<Header>;

  @ViewChild('footer', { static: true}) footerViewChild: ElementRef;

  constructor(public element: ElementRef) {
    this._element = this.element.nativeElement;
  }
  ngOnInit() {
      this.loading = true;
      if(this.imagePointer >= 0) {
      this.showRepeat = false;
      this.openGallery(this.imagePointer);
    } else {
      this.showRepeat = true;
    }
  }
  closeGallery() {
    this.opened = false;
    this.cancelEvent.emit(null);
  }
  prevImage() {
    this.loading = true;
    this.currentImageIndex--;
    if(this.currentImageIndex < 0) {
      this.currentImageIndex = this.modalImages.length-1  ;
    }
    this.openGallery(this.currentImageIndex);
  }
  nextImage() {
    this.loading = true;
    this.currentImageIndex++;
    if(this.modalImages.length === this.currentImageIndex) {
      this.currentImageIndex = 0;
    }
    this.openGallery(this.currentImageIndex);

  }
  openGallery(index) {
    if(!index) {
    this.currentImageIndex = 1;
    }
    this.currentImageIndex = index;
      this.opened = true;
     for (var i = 0; i < this.modalImages.length; i++) {
            if (i === this.currentImageIndex ) {
              this.imgSrc = this.modalImages[i].img;
              this.loading = false;
              break;
            }
       }
  }
}