import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { InfoProdukService } from 'src/app/core/services/infoproduk.service';
import { StorageUtilService } from 'src/app/shared/utils/storage-util.service';
import * as _ from 'lodash';
import { Channel } from 'src/app/core/models/channel';

@Component({
  selector: 'app-detail-channel',
  templateUrl: './detail-channel.component.html',
})
export class DetailChannelComponent implements OnInit {

  credentialsNasabahKey = 'nasabahCredentials';
  detailChannel = 'detailChannel';
  credentials: any;
  channel: any;
  labelText: '';

  listItem: Channel;
  listInfoChannel: any;
  listInfoProdukDefault: any;
  listInfoProduk: any;

  constructor(
    private router: Router,
    private infoProdukServices: InfoProdukService
  ) {
    this.listItem = new Channel();
   }

  ngOnInit() {
    this.infoProdukServices.getInfoProduk().subscribe(
      (response) => {
        this.listInfoProdukDefault = response.output_schema;
        // console.log(response);
        this.listInfoProduk = _.chain(response.output_schema)
          .groupBy('kategori')
          .map((value, key) => ({ kategori: key, products: value }))
          .value();

        this.getNama();

        // get list info channel
        var channel = _.filter(this.listInfoProdukDefault, { 'kategori': 'Channel' });
        this.listInfoChannel = _.chain(channel)
          .groupBy('kategori')
          .map((value, key) => ({ kategori: key, products: value }))
          .value();

        // console.log('channel', this.listInfoChannel);
      },
      (error) => {}
    );
  }

  isEmptyObject(obj) {
    return obj && Object.keys(obj).length === 0;
  }

  getNama() {
    this.credentials = StorageUtilService.getItem(this.credentialsNasabahKey);
    this.channel = StorageUtilService.getItem(this.detailChannel);
    // console.log('hasil', this.credentials);
    // console.log('hasil2', this.channel);

    this.labelText = this.credentials.namaNasabah;
    this.listItem = _.find(this.listInfoProdukDefault, {
      id: this.channel,
    });
    // console.log(this.listItem);
  }

  backToDash() {
    this.router.navigate(['dashboard'], { replaceUrl: true });
  }

  onClickBack() {
    localStorage.removeItem(this.detailChannel);
    this.router.navigate(['detail-produk'], { replaceUrl: true });
  }

}
