/**
 * The main component that renders single TabComponent
 * instances.
 */

import {
  Component,
  ContentChildren,
  QueryList,
  AfterContentInit,
  ViewChild,
  ComponentFactoryResolver,
  ViewContainerRef
} from '@angular/core';

import { TabCardComponent } from './tabCard.component';
//import { DynamicTabsDirective } from './dynamic-tabs.directive';

// <ul class="nav nav-tabs">
//         <li *ngFor="let tab of tabs" (click)="selectTab(tab)" [class.active]="tab.active">
//           <a href="#">{{tab.title}}</a>
//         </li>
//       </ul>
// [class.border-t-4]="tab.active"
// <button *ngFor="let tab of tabs"  (click)="selectTab(tab)" [ngClass]="{'active-tab': tab.active === true, 'nonactive-tab': tab.active === false }" class="py-2 px-6 block  focus:outline-none text-blue-bca font-18 font-weight-600"> {{tab.title}} </button>
@Component({
  selector: 'my-tab-cards',
  template: `
    <nav class="flex flex-col sm:flex-row   justify-center" >
      <div *ngFor="let tab of tabs" class="flex flex-col items-center py-2 px-6 block nonactive-font focus:outline-none font-16 font-weight-600 ">
        <label (click)="selectTab(tab)" class="pb-2 cursor-pointer" [ngClass]="{'active-font': tab.active === true, 'nonactive-font': tab.active === false }">{{tab.title}}</label>
        <div [ngClass]="{'active-tab': tab.active === true, 'nonactive-tab': tab.active === false }" style="width:32px; height:4px" class="flex mb-2 cursor-pointer"></div>
      </div>
    </nav>
    <ng-content></ng-content>
  `,
  styles: [
    `
    .active-font{
      color: #005CAB;
    }
    .nonactive-font {
      color: #808080;
    }
    .active-tab {
      border-top-width : 4px;
      border-bottom-width: 1px;
      border-left-width: 1px;
      border-right-width: 1px;
      border-color: #005CAB;
    }

    .nonactive-tab {
      border-top-width : 0px;
      border-bottom-width: 0px;
      border-left-width: 0px;
      border-right-width: 0px;
     
    }

    .tab-close {
      color: gray;
      text-align: right;
      cursor: pointer;
    }
    `
  ]
})
export class TabCardsComponent implements AfterContentInit {
  
  @ContentChildren(TabCardComponent) tabs: QueryList<TabCardComponent>;
  
  // contentChildren are set
  ngAfterContentInit() {
    // get all active tabs
    let activeTabs = this.tabs.filter((tab)=>tab.active);
    
    // if there is no active tab set, activate the first
    if(activeTabs.length === 0) {
      this.selectTab(this.tabs.first);
    }
  }
  
  selectTab(tab: TabCardComponent){
    // deactivate all tabs
    this.tabs.toArray().forEach(tab => tab.active = false);
    
    // activate the tab the user has clicked on.
    tab.active = true;
  }
}
