import { Injectable } from '@angular/core';
import { EncryptionUtilService } from './encryption-util.service';

@Injectable({
  providedIn: 'root'
})
export class StorageUtilService {
  constructor() {}

  /**
   * setItem
   * store item into storage
   */
  public static setItem(key: string, obj: any, global?: boolean) {
    const encryptedObject = EncryptionUtilService.encrypt(JSON.stringify(obj), true);
    if (global) {
      localStorage.setItem(key, encryptedObject);
    } else {
      sessionStorage.setItem(key, encryptedObject);
    }
  }

  /**
   * getItem
   * get item from storage
   * return object from storage
   */
  public static getItem(key: string, global?: boolean): any {
    let rawObj = '';
    if (global) {
      rawObj = localStorage.getItem(key);
    } else {
      rawObj = sessionStorage.getItem(key);
    }
    const decodedObj = EncryptionUtilService.decrypt(rawObj, true);
    //console.log(decodedObj);
    return JSON.parse(decodedObj);
  }

  /**
   * hasItem
   */
  public static hasItem(key: string, global?: boolean): boolean {
    if (global) {
      if (localStorage.getItem(key)) {
        return true;
      } else {
        return false;
      }
    } else {
      if (sessionStorage.getItem(key)) {
        return true;
      } else {
        return false;
      }
    }
  }
}
