import {Pipe} from "@angular/core";
import * as _ from "lodash";

/**
 * A simple string filter, since Angular does not yet have a filter pipe built in.
 */
@Pipe({
    name: 'stringFilter'
})
export class StringFilterPipe {

    transform(value: string[], q: string) {
        if (!q || q === '') {
            return value;
        }
        var data = value.filter(item => -1 < item['nama'].toLowerCase().indexOf(q.toLowerCase()));
        console.log(data);
        if(data.length < 1){
            //data = _.find(value, { tma_kode_bank: q }).toString();
            data = value.filter(item => -1 < item['tma_kode_bank'].toString().toLowerCase().indexOf(q.toLowerCase()));
        }
        return data;
    }
}
