import { Component, OnInit } from '@angular/core';
import { ConfirmationService } from 'src/app/shared/confirmdialog/confirmationservice';

@Component({
  selector: 'app-layar-verifikasi',
  templateUrl: './layar-verifikasi.component.html',
})
export class LayarVerifikasiComponent implements OnInit {

  images = [
    { thumb: './assets/gallery/img1_thumb.jpg', img: './assets/gallery/img1.jpg', description: 'Image 1' },
    { thumb: './assets/gallery/img2_thumb.jpg', img: './assets/gallery/img2.jpg', description: 'Image 2' }
  ];


  isShow1 = false;
  isShow2 = true;

  display1: boolean;
  display2: boolean;

  constructor(
    private confirmationService: ConfirmationService,
  ) {
    this.display1 = true;
    this.display2 = false;
  }

  ngOnInit() {
  }

  closeHeader() {
    this.confirmationService.confirm({
      icon: './assets/img/Info_Circle.png',
      acceptIcon: null,
      rejectIcon: null,
      rejectVisible: true,
      acceptVisible: true,
      acceptLabel: 'YA',
      rejectLabel: 'BATAL',
      header:  '',
      message: 'Apakah Anda yakin untuk check out' + '<br>' + 'nasabah?',
      closable: false,
      accept: () => {},
    });
  }



}
