import {
  Component,
  OnInit,
  ChangeDetectionStrategy,
  Input,
} from "@angular/core";
import { StorageUtilService } from "src/app/shared/utils/storage-util.service";
import { Router } from "@angular/router";
import { TreeviewItem, TreeviewConfig } from "ngx-treeview";

import { TabsComponent } from "../../shared/components/tabs/tabs.component";
import { InfoProdukService } from "./../../core/services/infoproduk.service";
import * as _ from "lodash";
import { NgxPaginationModule, PaginationInstance } from "ngx-pagination";
import { Produk } from './../../core/models/produk';
import { SettingCodeConstants } from './../../core/enums/setting-constants.enum';

@Component({
  selector: "app-detail-produk",
  templateUrl: "./detail-produk.component.html",
  styleUrls: ["./detail-produk.component.css"],
  changeDetection: ChangeDetectionStrategy.Default,
})
export class DetailProdukComponent implements OnInit {
  credentialsNasabahKey = "nasabahCredentials";
  credentials: any;
  labelText: "";
  items: TreeviewItem[];
  values: number[];
  config = TreeviewConfig.create({
    hasAllCheckBox: false,
    hasFilter: false,
    hasCollapseExpand: false,
    decoupleChildFromParent: false,
    maxHeight: 400,
  });
  public show: boolean = false;
  public showName: string = "";
  cardName: boolean = false;
  nameCard: string = '';

  listKategori: any;
  listInfoProduk: any;
  listInfoProdukDefault: any;
  listInfoChannel: any;
  selected = [];
  selectedChannel = [];


  listItem: Produk;
  informasiLainnyaPerludiketahui: any;

  listLimitBiayaKartu: any;

  tutorials: any;
  currentTutorial = null;
  currentIndex = -1;
  title = "";

  page = 1;
  // count = 0;
  // pageSize = 10;
  // pageSizes = [3, 6, 9];
  count = 0;

  tipeKartu: any;
  jenisKartu: any;
  rekeningFor = 1;
  relationship = 2;

  kartu = "Insert_Picture.png";

  public configTable: PaginationInstance = {
    id: "advanced",
    itemsPerPage: 10,
    currentPage: 1,
  };

  public filter: string = "";

  constructor(
    private router: Router,
    private infoProdukServices: InfoProdukService
  ) {
    this.listItem = new Produk();
  }

  onPageChange(number: number) {
    //this.logEvent(`pageChange(${number})`);
    this.configTable.currentPage = number;
  }

  onPageBoundsCorrection(number: number) {
    //this.logEvent(`pageBoundsCorrection(${number})`);
    this.configTable.currentPage = number;
  }

  ngOnInit() {
    this.rekeningFor = 1;
   
    this.infoProdukServices.getInfoProduk().subscribe(
      (response) => {
        this.listInfoProdukDefault = response.output_schema;
        // console.log(response)
        this.listInfoProduk = _.chain(response.output_schema)
          .groupBy("kategori")
          .map((value, key) => ({ kategori: key, products: value }))
          .value();

        this.getNama();

        this.listLimitBiayaKartu = _.chain(this.listItem.limitBiayaKartu)
          .groupBy("kartu")
          .map((value, key) => ({ kategori: key, keterangan: value }))
          .value();

        // console.log(this.listLimitBiayaKartu)

        // get list info channel
        var channel = _.filter(this.listInfoProdukDefault, { 'kategori': 'Channel' });
        this.listInfoChannel = _.chain(channel)
          .groupBy("kategori")
          .map((value, key) => ({ kategori: key, products: value }))
          .value();

        console.log('channel', this.listInfoChannel);
        
      },
      (error) => { 
       
      }
    );
  }

  backToDash() {
    this.router.navigate(["dashboard"], { replaceUrl: true });
  }

  getNama() {
    this.credentials = StorageUtilService.getItem(this.credentialsNasabahKey);
    console.log('detail-produk', this.credentials)

    this.nameCard = this.credentials.namaNasabah.toUpperCase();

    if (!this.isEmptyObject(this.credentials.products)) {
      console.log("credentials.products", this.credentials.products);

      // by id produk
      if (this.credentials.products['id'] !== undefined) {

        this.listItem = this.credentials.products;
        console.log('tidak undifined')
      } else {
        this.listItem = _.find(this.listInfoProdukDefault, {
          id: this.credentials.products[0],
        });
      }

      this.informasiLainnyaPerludiketahui = this.listItem["informasiLainya"][
        "perlu_diketahui"
      ]["perlu_diketahui"].split(";");
      // console.log(this.informasiLainnyaPerludiketahui);
    } else {
      this.listItem = new Produk();
    }

    this.labelText = this.credentials.namaNasabah;
  }

  toggle(kategori) {
    this.show = !this.show;

    // CHANGE THE NAME OF THE BUTTON.
    if (this.show) this.showName = "Hide";
    else this.showName = kategori;
  }

  // check if the item are selected
  checked(item) {
    if (this.selected.indexOf(item) != -1) {
      return true;
    }
  }

  // when checkbox change, add/remove the item from the array
  onChange(checked, item) {
    if (checked) {
      
      this.listItem = _.find(this.listInfoProdukDefault, { id: item });

      // console.log(this.listItem);

      console.log('listItem',this.listItem);
      //console.log('listLimitBiayaKartu[0].keterangan',this.listItem.listLimitBiayaKartu[0].keterangan)

      this.listLimitBiayaKartu = _.chain(this.listItem.limitBiayaKartu)
      .groupBy("kartu")
      .map((value, key) => ({ kategori: key, keterangan: value }))
      .value();

      this.informasiLainnyaPerludiketahui = this.listItem["informasiLainya"][
        "perlu_diketahui"
      ]["perlu_diketahui"].split(";");
      // console.log(this.informasiLainnyaPerludiketahui);

      this.retrieveTutorials();

      this.selected.push(item);
    } else {
      this.listItem = new Produk();
      this.selected.splice(this.selected.indexOf(item), 1);
    }

    if (this.selected.length > 1) {
     
      console.log(this.credentials.kategori);
      console.log(this.credentials.namaNasabah);
      console.log(this.selected);

      
      StorageUtilService.setItem(this.credentialsNasabahKey, {
        namaNasabah: this.credentials.namaNasabah,
        kategori: this.credentials.kategori,
        products: this.selected,
      });
      this.router.navigate(["infoProduk"], { replaceUrl: true });

    } else {
      // this.btnBandingkanValid = false;
    }
  }

  handlePageChange(event) {
    this.page = event;
    this.retrieveTutorials();
  }

  retrieveTutorials() {
    //const params = this.getRequestParams(this.title, this.page, this.pageSize);
    if (this.listItem["daftarBankPrima"].length < 1) {
      this.tutorials = []
    } else {
      this.tutorials = this.listItem["daftarBankPrima"];
      this.count = this.listItem["daftarBankPrima"].length;
    }


  }

  isEmptyObject(obj) {
    return obj && Object.keys(obj).length === 0;
  }

  unicodeToChar(text) {
    return text.replace(/\\u[\dA-F]{4}/gi, function (match) {
      return String.fromCharCode(parseInt(match.replace(/\\u/g, ""), 16));
    });
  }


  // check if the item are selected
  checkedChannel(item) {
    if (this.selectedChannel.indexOf(item) != -1) {
      return true;
    }
  }

  // when checkbox change, add/remove the item from the array
  onChangeChannel(checked, item) {
    if (checked) {
      this.selectedChannel.push(item);
    } else {
      this.selectedChannel.splice(this.selectedChannel.indexOf(item), 1);
    }
  }

  onChannelClick( item) {
    if (item) {
      StorageUtilService.setItem('detailChannel', item);
      this.router.navigate(["detail-channel"], { replaceUrl: true });
    } 
  }

  onItemChangeTipeKartu(value) {
    console.log(" Value is : ", this.jenisKartu);
    this.changeCard();
  }

  onItemChangeJenisKartu(value) {
    this.changeCard();
  }

  onItemChangeRelationship(value) {
    console.log('onItemChangeRelationship',value)
    if(value == 1){
      this.tipeKartu = SettingCodeConstants.tipeKartuKhusus;
      this.jenisKartu = SettingCodeConstants.jenisKartuGPN;
    }
   // console.log(" Value is : ", this.jenisKartu);
    this.changeCard();
  }

  onItemChangeCardName(value) {
    if(value == 1){
      this.cardName = true;
    }else{
      this.cardName = false;
    }
  }

  changeCard() {
    var path_kartu = 'kartu';
    var path = '';
    var cardName = '';
    var unNamed = '-UnNamed.png';

    if (this.tipeKartu == SettingCodeConstants.tipeKartuBlue) {
      cardName = "Blue";
    } else if (this.tipeKartu == SettingCodeConstants.tipeKartuGold) {
      cardName = "Gold";
    } else if (this.tipeKartu == SettingCodeConstants.tipeKartuPlatinum) {
      cardName = "Platinum";
    } else {
      cardName = "Blue";
    }



    if(this.jenisKartu === undefined){
      console.log('disini')
    }else{
       
      // if (this.tipeKartu == 4) {
      //   path = 'MC';
      //   this.kartu = path_kartu + '/' + path + '/' + cardName + unNamed;
      // }else{
        if (this.jenisKartu == SettingCodeConstants.jenisKartuGPN) {
          path = 'GPN';
          this.kartu = path_kartu + '/' + path + '/' + cardName + unNamed;
        } 
        else {
          path = 'MC';
          this.kartu = path_kartu + '/' + path + '/' + cardName + unNamed;
        }
      //} 
     
    }
    
  }


}
