import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-detail-verifikasi',
  templateUrl: './detail-verifikasi.component.html',
})
export class DetailVerifikasiComponent implements OnInit {

  condition = true;
  change = false;
  isShow1 = false;
  isShow2 = false;

  constructor() { }

  ngOnInit() {
    this.loadingReader();
  }

  wait(timeout) {
    return new Promise(resolve => {
        setTimeout(resolve, timeout);
    });
  }

  loadingReader = async () => {
    await this.wait(5000);
    this.condition = false;
    this.change = true;
    this.isShow1 = true;
  }

  coba() {}


}
